import styled from 'styled-components';
import { Row, Col, Button as BS } from 'react-bootstrap';
import SVG from "react-inlinesvg";
import {ETHER} from "@uniswap/sdk";
import {useState} from "react";

import Page from "../../components/Page";
import DefaultCard from "../../components/Card";
import BridgeInputPanel from "../../components/BridgeInputPanel";
import SwapIcon from '../../assets/images/cross/swap.svg';
import AddressInputPanel from "../../components/AddressInputPanel";

const BridgeType = {
	"SWAP": "SWAP",
	"SEND": "SEND"
}


const TabButton = styled(BS)`
  min-height: 48px;
  height: 48px;
  border-radius: 18px;
  font-weight: 500;
  font-size: 1rem;
  min-width: 127px;
  
  @media (max-width: 991px) {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-size: .875rem;
    min-height: 40px;
    height: 40px;
    padding: 0;
    border-radius: 12px;
    min-width: 105px;
  }
`

const Button = styled(BS)`
  min-height: 48px;
  height: 48px;
  min-width: 205px;
`

const AlertButton = styled(BS)`
  min-height: 48px;
  height: 48px;
  border-radius: 18px;
  font-weight: 500;
  min-width: 105px;
`

const Title = styled.h1`
	margin: 0;
	font-size: 2.5rem;
	font-weight: 700;
	color: ${({ theme }) => theme.text1};
	line-height: 3rem;

	
	@media (max-width: 1199px) {
		font-size: 2.25rem;
	}
	@media (max-width: 991px) {
		font-size: 2rem;
	  	margin-bottom: 0.5rem;
	}
	@media (max-width: 767px) {
		font-size: 1.5rem;
	}
`


const Header = styled.div`
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 20px;
	margin-top: 24px;
	
	@media(max-width: 991px) {
		margin-top: 10px;
	  	flex-direction: column;
	  	align-items: stretch;
	}
`

const Card = styled(DefaultCard)`
  margin-bottom: 1rem;
  
  & > .card-body {
    padding: 36px 64px 24px;


    @media (max-width: 991px) {
      padding: 24px;
    }

    @media (max-width: 576px) {
      padding: 16px;
    }
  }
`

const TabHeader = styled.div`
  display: grid;
  grid-column-gap: 25px;
  grid-template-columns: 127px 127px;
  border-radius: 20px;
  
  @media (max-width: 991px) {
    padding: 8px;
    background-color: ${({ theme }) => theme.modalBG};
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 16px;
    border: 1px solid ${({ theme }) => theme.bg5};
  }
  
  @media (max-width: 576px) {
    grid-template-columns: 1fr;
    grid-row-gap: 12px;
    grid-column-gap: 0;
    
  }
`

const SwapCurrencies = styled.div`
	display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
  cursor: pointer;


  @media (max-width: 576px) {
    padding: 8px;

  }
  
`

const SubmitButtonContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 32px;
  
  @media (max-width: 1199px) {
    margin-top: 28px;
  }
  
  @media (max-width: 991px) {
    margin-top: 24px;
    
  }
  
  @media (max-width: 767px) {
    margin-top: 16px;
    align-items: stretch;
  }
  
`

const AlertContainer = styled.div`
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4px 4px 4px 64px;
  background-color: ${({ theme }) => theme.primaryLight};

  
  @media (max-width: 1199px) {
    padding-left: 48px;
  }

  @media (max-width: 991px) {
    padding-left: 36px;
  }

  @media (max-width: 767px) {
    padding-left: 16px;
  }
  
`

const AlertText = styled.span`
  font-weight: 500;
  font-size: 1rem;
  color: ${({ theme }) => theme.primary};
  line-height: 21px;
  padding-right: 1rem;
`

const CrossAnySwap = props => {
	const [showAlert, setShowAlert] = useState(true);
	const [side, setSide] = useState(BridgeType.SWAP);
	const [inputCurrency, setInputCurrency] = useState(ETHER);
	const [outputCurrency, setOutputCurrency] = useState(undefined);
	const [receiptAddress, setReceiptAddress] = useState('');

	const [inputAmount, setInputAmount] = useState('');
	const [outputAmount, setOutputAmount] = useState('');


	const toggleCurrency = () => {
		const tempInputAmount = inputAmount;
		const tempOutputAmount = outputAmount;

		setInputCurrency(outputCurrency);
		setOutputCurrency(inputCurrency);

		setInputAmount(tempOutputAmount);
		setOutputAmount(tempInputAmount)
	}

	return (
		<Page>
			<Row>
				<Col xs={12} lg={{ span: 10, offset: 1 }} xl={{ span: 8, offset: 2 }}>
					<Header>
						<Title>{side === BridgeType.SWAP ? "Swap" : "Send"}</Title>

						<TabHeader>
							<TabButton
								onClick={setSide?.bind(this, BridgeType?.SWAP)}
								variant={side === BridgeType?.SWAP ? 'primary' : 'light-primary'}
							>
								Swap
							</TabButton>
							<TabButton
								onClick={setSide?.bind(this, BridgeType?.SEND)}
								variant={side === BridgeType?.SEND ? 'primary' : 'light-primary'}
							>
								Send
							</TabButton>
						</TabHeader>
					</Header>
				</Col>
				<Col xs={12} lg={{ span: 10, offset: 1 }} xl={{ span: 8, offset: 2 }}>
					<Card>
						<Row>
							<Col xs={12}>
								<BridgeInputPanel
									value={inputAmount}
									onUserInput={setInputAmount}
									onMax={setInputAmount}
									label={"Input"}
									onCurrencySelect={setInputCurrency}
									currency={inputCurrency}
									otherCurrency={outputCurrency}
									id={'bridge-input-currency'}
									showCommonBases={false}
									withoutMargin={true}
								/>
							</Col>
							<Col xs={12} className={'d-flex align-items-center justify-content-center'}>
								<SwapCurrencies onClick={toggleCurrency}>
									<SVG src={SwapIcon} width={24} height={24}/>
								</SwapCurrencies>
							</Col>
							<Col xs={12}>
								<BridgeInputPanel
									value={outputAmount}
									onUserInput={setOutputAmount}
									onMax={setOutputAmount}
									label={"Output"}
									onCurrencySelect={setOutputCurrency}
									currency={outputCurrency}
									otherCurrency={inputCurrency}
									id={'bridge-output-currency'}
									showCommonBases={false}
									withoutMargin={true}
								/>
							</Col>
							{side === BridgeType.SEND && (
								<Col xs={12} className={'pt-4'}>
									<AddressInputPanel
										value={receiptAddress}
										onChange={e => setReceiptAddress(e.target.value)}
										withoutMargin={true}
										label={"Recipient Address"}
									/>
								</Col>
							)}
						</Row>
					</Card>
				</Col>
				{showAlert && (
					<Col xs={12} lg={{ span: 10, offset: 1 }} xl={{ span: 8, offset: 2 }}>
						<AlertContainer>
							<AlertText>This project is in data. Use at your own risk.</AlertText>
							<AlertButton onClick={setShowAlert.bind(this, false)}>Agree</AlertButton>
						</AlertContainer>
					</Col>
				)}
				<Col xs={12} lg={{ span: 10, offset: 1 }} xl={{ span: 8, offset: 2 }}>
					<SubmitButtonContainer>
						<Button variant={'primary'}>{side === BridgeType.SWAP ? "Swap" : "Send"}</Button>
					</SubmitButtonContainer>
				</Col>
			</Row>
		</Page>
	)
}

export default CrossAnySwap;

